@import url(https://rsms.me/inter/inter.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/croppie/2.6.2/croppie.min.css);
::-webkit-scrollbar { 
  display: none; 
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Inter", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background: url(../../static/media/pizzagame2-logo-1920x1080.01abd22d.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-color: #0c0c0c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 50px;
}

a {
  color: #61dafb;
}

.svg-icons{
  transform: scale(1.5);
}

#webcam-text {
  line-height: 2em;
}

